import React from "react";
import ReactDOM from "react-dom/client";
import "./index.css";
import App from "./App";
import reportWebVitals from "./reportWebVitals";
// 
import { registerMicroApps, start } from "qiankun";

//
registerMicroApps([
  {
    // 
    name: "qk-micro-react",
    // id 
    container: "#qk-micro-react",
    // 
    // entry: 'http://localhost:3011', // 
    entry: "https://onehulu.github.io/qiankun-mirco-react/", //github
    // 
    activeRule: "/qk-micro-react",
  },
  {
    name: "qk-micro-vue",
    container: "#qk-micro-vue",
    // entry: "http://localhost:3012/",// 
    entry: "https://onehulu.github.io/qiankun-mirco-vue/", //github
    activeRule: "/qk-micro-vue",
  },
]);

// qiankun
start({
  sandbox: {
    // 
    experimentalStyleIsolation: true,
  },
});

const root = ReactDOM.createRoot(
  document.getElementById("root") as HTMLElement
);
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
