import React, { useState } from "react";
import { BrowserRouter } from "react-router-dom";
import { Layout, Menu, theme } from "antd";
import HeadMenuItemsList from "./menu/menu-item-head";
import "./App.css";

const { Header, Content } = Layout;

const App: React.FC = () => {
  // andt 
  const {
    token: { colorBgContainer },
  } = theme.useToken();

  //  key
  const [menuKey, setMenuKey] = useState(0);

  // 
  const isInMainProject = ["/", "/qiankun-base","/qiankun-base/"].includes(
    window.location.pathname
  );

  return (
    <div className="App">
      <BrowserRouter>
        <Layout className="layout">
          <Header>
            <div className="logo" />
            <Menu
              theme="dark"
              mode="horizontal"
              items={HeadMenuItemsList.map((item, index) => {
                return {
                  key: index,
                  label: item?.label,
                };
              })}
              onSelect={(e: any) => {
                //  key
                setMenuKey(e.key);
                // 
                window.history.pushState(
                  null,
                  "",
                  HeadMenuItemsList[e.key].hrefPath
                );
              }}
            />
          </Header>

          <Content>
            <div
              className="site-layout-content"
              style={{ background: colorBgContainer }}
            >
              {/*  */}
              {isInMainProject ? (
                <div
                  style={{
                    fontSize: "30px",
                  }}
                >
                  ,github pages 
                </div>
              ) : (
                <div id={HeadMenuItemsList[menuKey]?.id} /> // id  registerMicroApps  container 
              )}
            </div>
          </Content>
        </Layout>
      </BrowserRouter>
    </div>
  );
};

export default App;
